<template>
  <div class="home">
    <span v-show="this.$parent.curlang === 1">
    <div class="container-fluid containerMod">
      <br>
      <div class="row">
        <div class="col-md-4">
          <!-- sub-title=" /" -->
          <b-card header="<h6> </h6>"
            class="mb-2 bg2"
            title=" "
            show-footer
            align="left"
            >
            <img class=".roundborder" src="../assets/avatar.jpg" width="250" height="250">
            <br>
            <small> /</small>
            <br><br>
            <p> {{ myage }}.<br>   .</p>
            <small slot="footer" class="text-muted">
              <a href="mailto:to-timur@yandex.ru"><icon name="envelope" scale="2" ></icon></a>
              <a href="https://github.com/GTimur" alt="github link"> <icon name="github" scale="2"></icon></a>
            </small>
          </b-card>
        </div>
        <div class="col-md-8">
          <b-card header="<h6> </h6>"
            class="mb-2 bg2 display: none;"
            show-footer
            align="left">             
          <h6> 10           ,      ( 7  -       Oracle),        (    , -),        (      ,   )     (C/C++, JavaEE, C#, Python, Golang).</h6>
          <br>
          <h6>     .       Golang, C#, Java  PL/SQL.</h6>
          <br>
          <h6>         Golang.</h6>
            <small slot="footer" class="text-muted">
            </small>
          </b-card>
        </div>
        <div class="col-md-0">
        </div>
      </div>        
    </div>
    </span>
    <span v-show="this.$parent.curlang === 2">
      <homeen></homeen>
    </span>
  </div>
</template>

<script>
var moment = require('moment')
import HomeEN from '@/components/HomeEN'

export default {
  name: 'home',
  data () {
    return {
      myage: moment().diff('1983-01-27', 'years')
    }
  },
  components: {
    homeen: HomeEN
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.containerMod {
opacity: .9; 
margin: 0px auto;
}


img {
  max-width: 100%;
  height: auto;
  width: auto\9; /* ie8 */
}

.bg2 {
  background-color: #FFFFF0;
}

img, .roundborder {
-moz-border-radius:1px;
-khtml-border-radius:1px;
-webkit-border-radius:1px;
border-radius:10px;
/*border:1px solid white;*/
}

.notvisible {
  display: none;
}

</style>



