<template>
    <div class="mainmenu">        
        <div class="row lpad">
            <div class="col-md-1">
            </div>
            <div class="col-md-10">
                <b-nav links class="bg roundborder">
                    <b-nav-item>
                        <router-link to="/">
                        <span v-on:click="currentpath = 'Main'">
                        {{ this.getmenuitems(1) }}</span>
                        </router-link>
                    </b-nav-item>
                    <b-nav-item>
                        <router-link to="/about">
                        <span v-on:click="currentpath = 'About'">
                        {{ this.getmenuitems(2) }}
                        </span>
                        </router-link>
                    </b-nav-item>
                    <b-nav-item>
                        <router-link to="/folio">
                        <span v-on:click="currentpath = 'Folio'">
                        {{ this.getmenuitems(3) }}
                        </span>                        
                        </router-link>
                    </b-nav-item>
                    <b-nav-item>
                        <router-link to="/notes">
                        <span v-on:click="currentpath = 'Notes'">
                        {{ this.getmenuitems(4) }}
                        </span>                        
                        </router-link>
                    </b-nav-item>
                    <b-nav-item>
                        <router-link to="/download">
                        <span v-on:click="currentpath = 'Download'">
                        {{ this.getmenuitems(5) }}
                        </span>                        
                        </router-link>
                    </b-nav-item>

                    <!--b-nav-item>
                        <router-link to="">
                        <span v-on:click="greet">
                        Debuglink
                        </span>
                        </router-link>                        
                    </b-nav-item>
                    <b-nav-item disabled>
                        <router-link to="">                        
                        {{ currentpath }}
                        </router-link>                        
                    </b-nav-item-->
                    <b-nav-item>
                        <router-link to="">                        
                        |
                        </router-link>
                    </b-nav-item>
                    <b-nav-item>
                        <router-link to="">                        
                        <span align = "right" v-on:click="this.$parent.changelang"><a href="javascript:void(0)">{{ this.$parent.getcurlangphr() }} {{ this.$parent.getcurlang() }}</a></span>
                        </router-link>
                    </b-nav-item>                  
                </b-nav>                    
            </div>
        </div>        
    </div>
</template>

<script>
export default {
  name: 'mainmenu',
  data: function () {
    return {
      currentpath: ''
    }
  },
  methods: {
    getcurlang: function () {
      return this.$parent.curlang
    },
    getmenuitems: function (index) {
      switch (index) {
        case 1: if (this.getcurlang() === 1) return ''
          else return 'Home'
        case 2: if (this.getcurlang() === 1) return ' '
          else return 'Work experiece'
        case 3: if (this.getcurlang() === 1) return ''
          else return 'Portfolio'
        case 4: if (this.getcurlang() === 1) return ''
          else return 'My notes'
        case 5: if (this.getcurlang() === 1) return ''
          else return 'Download'
        default: return ''
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

a {
     color: #FFFFFF; /*#42b983;*/
}
.lpad {
  padding-left:10em;
  padding-right:10em;
}

.bg {
  background-color: #A9A9A9;
}

.bg2 {
  background-color: #FFFFF0;
}

.roundborder {
-moz-border-radius:4px;
-khtml-border-radius:4px;
-webkit-border-radius:4px;
border-radius:4px;
/*border:1px solid white;*/
}

/* xs -  ( 576px) */
/* CSS  ,   575px () */
 
/* sm- (   576px) */
@media (min-width: 576px) {
  /* CSS : 576px <=  <= 767px */
  .lpad {
  padding-left:0em;
  padding-right:0em;

}
}
 
/* md- (   768px) */
@media (min-width: 768px) {
  /* CSS : 768px <=  <= 991px */
    .lpad {
    padding-left:2em;
  padding-right:2em;

}
}
 
/* lg- (   992px) */
@media (min-width: 992px) {
  /* CSS : 992px <=  <= 1119px */
    .lpad {
    padding-left:5em;
  padding-right:5em;

}
}
 
/* xl- (   1200px) */
@media (min-width: 1200px) {
  /* CSS :  >= 1200px */
    .lpad {
    padding-left:10em;
    padding-right:10em;

}
}

</style>



